import React, {Component} from 'react';
import {connect} from 'react-redux';

import './Skills.css';

import Slider from 'react-slick';
import map from 'lodash/map';
import flatten from 'lodash/flatten';
import Container from 'reactstrap/es/Container';
import Row from 'reactstrap/es/Row';
import Col from 'reactstrap/es/Col';
import Progress from 'reactstrap/es/Progress';
import Fade from 'react-reveal/Fade';
import VisibilitySensor from 'react-visibility-sensor';
import LazyLoad from 'react-lazyload';

import skills from '../shared/data/skills.json';
import skillBars from '../shared/data/skillBars'

const skillsList = flatten(map(skills, 'skills'));

class Skills extends Component {

  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      barContainerVisible: false
    };
    this.barContainerVisibility = this.barContainerVisibility.bind(this)
  }

  componentDidMount() {

  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  barContainerVisibility = (isVisible) => {
    if(isVisible){
      this.setState({barContainerVisible: true})
    }
  }

  render() {
    const {width} = this.state;
    const isMobile = width <= 768;
    const settings = {
      infinite: true,
      slidesToShow: isMobile ? 2 :4,
      slidesToScroll: isMobile ? 2 :4,
      autoplay: true,
      autoplaySpeed: 3000,
      arrows: false
    };
    return (
      <div>
        <Container className={'skills-container'}>
          <h2 className={'title'}>Professional Skills</h2>
          <Fade top distance={'50px'}>
            <VisibilitySensor onChange={this.barContainerVisibility} active={!this.state.barContainerVisible} partialVisibility>
              <Container className={'bars-container'}>
                {
                  skillBars.map(skill => {
                    return (
                      <div key={skill.name} className={'skill-name-bar'}>
                        <Row>
                          <Col xs={4}>{skill.name}</Col>
                          <Col xs={8} className={'text-right'}>{skill.description}</Col>
                        </Row>
                        <Progress className={'skill-progress-bar'} value={this.state.barContainerVisible ? skill.confidence: 0} />
                      </div>
                    )
                  })
                }
              </Container>
            </VisibilitySensor>
          </Fade>
          <h2 className={'title'}>Technologies Used</h2>
          <Fade top distance={'50px'}>
          <Container className={'technologies-container'}>
            <Slider {...settings}>
              {
                skillsList.map((skill)=>{
                  return (
                    <div className={'skill-image'} key={skill.title}>
                      <LazyLoad>
                        <img src={skill.image} title={skill.title} alt={skill.title}/>
                      </LazyLoad>
                    </div>
                  )
                })
              }
            </Slider>
          </Container>
          </Fade>
        </Container>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    default: state.default
  };
}

export default connect(mapStateToProps, {})(Skills);
