import React, {Component} from 'react';
import {connect} from 'react-redux';

import './Json.css';

import Container from 'reactstrap/es/Container';
import Button from 'reactstrap/es/Button';
import Collapse from 'reactstrap/es/Collapse';
import Fade from 'react-reveal/Fade';

import resume from '../shared/data/resume';

class Json extends Component {

  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      collapse: false
    };
    this.toggle = this.toggle.bind(this);
  }

  componentDidMount() {

  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  render() {
    return (
      <div>
        <Container className={'json-container'}>
          <h2 className={'title'}>Json</h2>
          <Fade top distance={'50px'}>
            <Container className={'data-container'}>
              <Button color={'outer'} className={`ripple ${this.state.collapse?'is-hover':''}`} size={'lg'} block onClick={this.toggle}>Click to view JSON</Button>
              <Collapse isOpen={this.state.collapse}>
                <div style={{backgroundColor: 'rgba(0,0,0,.05)', padding: '10px 20px'}}>
                  <pre>
                    {
                      JSON.stringify(resume, null, 2)
                    }
                  </pre>
                </div>
              </Collapse>
            </Container>
          </Fade>
        </Container>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    default: state.default
  };
}

export default connect(mapStateToProps, {})(Json);
