import React, {Component} from 'react';
import {connect} from 'react-redux';

import './Header.css';

import { Events } from 'react-scroll';
import BootstrapScrollLink from '../shared/BootstrapScrollLink';
import Container from 'reactstrap/es/Container';
import NavbarBrand from 'reactstrap/es/NavbarBrand';
import Nav from 'reactstrap/es/Nav';
import Navbar from 'reactstrap/es/Navbar';
import NavItem from 'reactstrap/es/NavItem';
import Tooltip from 'reactstrap/es/Tooltip';
import Button from 'reactstrap/es/Button';
import NavbarToggler from 'reactstrap/es/NavbarToggler';
import Collapse from 'reactstrap/es/Collapse';

import {downloadResume} from '../../actions/downloadActions';
import CaptchaModal from '../shared/CaptchaModal';

const categories = ['about', 'skills', 'experience', 'education', 'certification', 'json', 'yaml', 'connect'];

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      spy: true,
      tooltipOpen: false,
      isOpen: false,
      isMobile: this.isMobile(window.innerWidth),
      isModalOpen: false
    };
    this.toggle = this.toggle.bind(this);
    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.modalToggle = this.modalToggle.bind(this);
    this.download = this.download.bind(this);
  }

  isMobile(width){
    return (width <= 768)
  }

  componentDidMount() {
    Events.scrollEvent.register('begin', this.handleScrollStart);
    Events.scrollEvent.register('end', this.handleScrollEnd);
  }


  componentWillUnmount() {
    Events.scrollEvent.remove('begin');
    Events.scrollEvent.remove('end');
  }

  handleSetActive = (to) => {
    this.setState({currentCategory: to})
  };

  handleScrollStart = (to) => {
    this.setState({spy: false, currentCategory: to})
  };

  handleScrollEnd = (to) => {
    this.setState({spy: true, currentCategory: to})
  };

  toggle() {
    this.setState({
      tooltipOpen: !this.state.tooltipOpen
    });
  }

  toggleNavbar() {
    if(this.state.isMobile){
      this.setState({
        isOpen: !this.state.isOpen
      });
    }
  }

  download(fileName, captchaToken) {
    setTimeout(() => {
      this.setState(prevState => ({
        isModalOpen: !prevState.isModalOpen,
        tooltipOpen: !prevState.tooltipOpen,
        isOpen: !prevState.isOpen
      }));
      this.props.downloadResume(fileName, captchaToken);
    }, 500);
  }

  modalToggle(fileName){
    this.setState(prevState => ({
      isModalOpen: !prevState.isModalOpen,
      tooltipOpen: false,
      fileName
    }));
  }

  render() {
    const {spy, currentCategory} = this.state;
    return (
      <Container fluid className='top-container'>
        <Container>
          <Navbar expand="xl">
            <NavbarBrand href="/">
              <h1 className={'top-name'} style={{}}>Sailesh Kotha</h1>
              <h2 className={'top-title'} style={{}}>Software Engineer</h2>
            </NavbarBrand>
            <NavbarToggler onClick={this.toggleNavbar} className={'ripple navbar-toggle btn-outer'}>
              MENU
              {/*<Button color={'outer'} className={'ripple navbar-toggle'}>MENU</Button>*/}
            </NavbarToggler>
            <Collapse isOpen={this.state.isOpen} navbar>
            <Nav navbar className="ml-auto" style={{fontSize: 13, fontWeight: 700, lineHeight: 3, color: '#3d4451', cursor: 'pointer'}}>
              {
                categories.map((category)=>{
                  return (<BootstrapScrollLink onClick={this.toggleNavbar} activeClass={' '} className={`mx-3 ${currentCategory === category ? 'selected':''}`} spy={spy} key={category} to={category} smooth={true} offset={-65}
                                       duration={500} onSetActive={this.handleSetActive}>
                    {category.toUpperCase()}
                  </BootstrapScrollLink>)
                })
              }
              <NavItem className={'mx-3'} id={'download'}>
                DOWNLOAD
              </NavItem>
              <Tooltip placement="bottom" hideArrow trigger={'hover'} autohide={false} isOpen={this.state.tooltipOpen} target="download" toggle={this.toggle}>
                <Button onClick={this.modalToggle.bind(this, 'Resume.pdf')} color={'outer'} className={'tooltip-button ripple'}>
                  PDF
                </Button>
                <Button onClick={this.modalToggle.bind(this, 'Resume.docx')} color={'outer'} className={'tooltip-button ripple'}>
                  DOC
                </Button>
              </Tooltip>
            </Nav>
            </Collapse>
          </Navbar>
        </Container>
        <CaptchaModal download={this.download} fileName={this.state.fileName} isModalOpen={this.state.isModalOpen} modalToggle={this.modalToggle}/>
      </Container>
    );
  }
}

function mapStateToProps(state) {
  return {
    default: state.default
  };
}

export default connect(mapStateToProps, {downloadResume})(App);
