import React, {Component} from 'react';
import {Button, Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import ReCAPTCHA from 'react-google-recaptcha';

import './CaptchaModal.css';

class CaptchaModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile: this.isMobile(window.innerWidth),
      isModalOpen: false
    };
    this.verifyCallback = this.verifyCallback.bind(this);
  }

  isMobile(width) {
    return (width <= 768);
  }

  handleWindowSizeChange = () => {
    this.setState({ isMobile: this.isMobile(window.innerWidth) });
  };

  static getDerivedStateFromProps(nextProps, prevState){
    if(nextProps.isModalOpen!==prevState.isModalOpen){
      return {isModalOpen: nextProps.isModalOpen};
    }
    else {
      return null;
    }
  }

  verifyCallback(captchaToken) {
    this.props.download(this.props.fileName, captchaToken)
  }

  render() {
    return (
      <div>
        <Modal isOpen={this.state.isModalOpen} toggle={this.props.modalToggle} centered={true}>
          <ModalHeader toggle={this.props.modalToggle}>Download</ModalHeader>
          <ModalBody className={'modalBody'}>
            <ReCAPTCHA
              sitekey="6LcItKoUAAAAAKcXgvpbW7SewIUAgLozLoseMogM"
              onChange={this.verifyCallback}
              size="normal"
              className={this.state.isMobile ? 'mobile-captcha' : 'desktopCaptcha'}
            />
          </ModalBody>
        </Modal>
      </div>
    );
  }
}

export default CaptchaModal;
