import React, {Component} from 'react';
import { ScrollLink } from 'react-scroll'
import NavItem from 'reactstrap/es/NavItem';

class BootstrapScrollLink extends Component {
	render() {
		return (
			<NavItem {...this.props}>
				{this.props.children}
			</NavItem>
		);
	}
}

export default ScrollLink(BootstrapScrollLink);
