import React, {Component} from 'react';
import {connect} from 'react-redux';

import './Experience.css';

import Container from 'reactstrap/es/Container';
import Fade from 'react-reveal/Fade';

import resume from '../shared/data/resume';

const experiences = resume.Experience;

class Experience extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isMobile: this.isMobile(window.innerWidth)
    };
  }

  isMobile(width){
    return (width <= 768)
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ isMobile: this.isMobile(window.innerWidth) });
  };

  render() {
    return (
      <div>
        <Container className={'experience-container'}>
          <h2 className={'title'}>Work Experience</h2>
          <ul className="timeline">
            {
              experiences.map((experience, i)=>{
                return (
                  <Fade key={experience['Company Name']} left={!(i%2) && !this.state.isMobile} right={Boolean(i%2) || this.state.isMobile} distance={'30px'} duration={700}>
                  <li className={i%2?'timeline-inverted':''}>
                    <div className="timeline-badge">
                      <i className="fa fa-circle"/>
                    </div>
                    <div className="timeline-panel">
                      <div className="date">{experience.FromTo}</div>
                      <div className="timeline-heading">
                        <h4>{experience['Company Name']}</h4>
                      </div>
                      <h4 className={'role'}>{experience['Title']}</h4>
                      <div className="timeline-body">
                        {
                          experience.Description.map((eachDescription, i)=>{
                            return (<p key={i}><span>{eachDescription}</span></p>)
                          })
                        }
                      </div>
                    </div>
                  </li>
                  </Fade>
                )
              })
            }
            <li className="clearfix no-float"></li>
          </ul>
        </Container>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    default: state.default
  };
}

export default connect(mapStateToProps, {})(Experience);
