import axios from 'axios';
import types from '../actionTypes';
import delay from 'delay';
import { apiEndpoint } from './parameters';

export const submitForm = (form, captchaToken) => {
  return async (dispatch) => {
    try{
      await axios.post(`${apiEndpoint}/submit-form`, {
        form, captchaToken
      });
      dispatch({
        type: types.submitFormSuccessResponse
      })
    }catch(error) {
      /*eslint no-console:0*/
      console.error(error)
    }
    await delay(1000);
    dispatch({
      type: types.resetFormStatus
    })
  }
};
