import React, { Component } from 'react';
import { connect } from 'react-redux';
import propTypes from 'prop-types';

import './Connect.css';
import 'react-toastify/dist/ReactToastify.css';

import Container from 'reactstrap/es/Container';
import Row from 'reactstrap/es/Row';
import Col from 'reactstrap/es/Col';
import Button from 'reactstrap/es/Button';
import Fade from 'react-reveal/Fade';
import LazyLoad from 'react-lazyload';
import ReCAPTCHA from 'react-google-recaptcha';
import { toast, Flip } from 'react-toastify';

import {submitForm} from '../../actions';

import MapContainer from './MapContainer';
import socials from '../shared/data/socials';


class Connect extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isMobile: this.isMobile(window.innerWidth),
      inProgress: false,
      done: false,
      captchaToken: null
    };

    this.updateFormState = this.updateFormState.bind(this);
    this.submitForm = this.submitForm.bind(this);
    this.verifyCallback = this.verifyCallback.bind(this);
    this.toastId = null;
  }

  isMobile(width) {
    return (width <= 768);
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ isMobile: this.isMobile(window.innerWidth) });
  };

  componentDidUpdate(prevProps) {
    if (this.props.form.inProgress !== prevProps.form.inProgress || this.props.form.done !== prevProps.form.done) {
      this.setState({
        inProgress: this.props.form.inProgress,
        done: this.props.form.done
      })
    }
  }

  updateFormState = (evt) => {
    const field = evt.target.name;
    let form = Object.assign({}, this.state.form);
    form[field] = evt.target.value;
    this.setState({
      form
    });
  };

  submitForm(evt) {
    evt.preventDefault();

    if(!this.state.captchaToken){
      if(!toast.isActive(this.toastId)) {
        this.toastId = toast.error('Please Submit Captcha', {
          position: toast.POSITION.BOTTOM_CENTER,
          autoClose: 3000,
          transition: Flip,
          delay: 0,
          hideProgressBar: true,
          bodyClassName: 'toastBody'
        });
      }
      return
    }

    toast.dismiss();

    this.setState({
      inProgress: true,
      done: false
    });
    this.props.submitForm(this.state.form, this.state.captchaToken);
  }

  verifyCallback(captchaToken) {
    this.setState({
      captchaToken
    });
  }


  render() {
    return (
      <div>
        <Container className={'connect-container'}>
          <h2 className={'title'}>Contact Me</h2>
          <Row>
            <Col md={6}>
              <Fade left={!this.state.isMobile} up={this.state.isMobile} distance={'50px'}
                    duration={700}>
                <div className={'contact-form-box card'}>
                  <h3>Feel free to contact me</h3>
                  <form onSubmit={this.submitForm}>
                    <div className="input-container">
                      <input type="text" name={'name'} value={this.state.name} onChange={this.updateFormState}
                             id="name" required="required"/>
                      <label htmlFor="name">Name</label>
                      <div className="bar"/>
                    </div>
                    <div className="input-container">
                      <input type="text" name={'email'} value={this.state.email} onChange={this.updateFormState}
                             id="email" required="required"/>
                      <label htmlFor="email">Email</label>
                      <div className="bar"/>
                    </div>
                    <div className="input-container">
                      <input type="text" name={'subject'} value={this.state.subject} onChange={this.updateFormState}
                             id="subject" required="required"/>
                      <label htmlFor="subject">Subject</label>
                      <div className="bar"/>
                    </div>
                    <div className="input-container">
                      <textarea rows={4} name={'message'} value={this.state.message} onChange={this.updateFormState}
                                id="description" required="required"/>
                      <label htmlFor="description">Message</label>
                      <div className="bar"/>
                    </div>
                    <ReCAPTCHA
                      sitekey="6LcItKoUAAAAAKcXgvpbW7SewIUAgLozLoseMogM"
                      onChange={this.verifyCallback}
                      size="normal"
                      className={this.state.isMobile ? 'mobile-captcha' : ''}
                    />
                    <Button color={'outer'}
                            className={'in-progress ripple send-button'}>
                      {this.state.inProgress ?
                        <i className="notched circle loading icon"/> : this.state.done ?
                          <i className="check icon"/> : 'SEND'}
                    </Button>
                  </form>
                </div>
              </Fade>
            </Col>
            <Col md={6}>
              <Fade right={!this.state.isMobile} up={this.state.isMobile} distance={'50px'}
                    duration={700}>
                <div className={'contact-info-box'}>
                  <div className={'contact-info-list'}>
                    <Row className={'contact-profile-item'}>
                      <Col xs={3} className={'contact-profile-item-name'}>
                        Phone
                      </Col>
                      <Col xs={9} className={'content'}>
                        +1 (602) 820-1615
                      </Col>
                    </Row>
                    <Row className={'contact-profile-item'}>
                      <Col xs={3} className={'contact-profile-item-name'}>
                        Skype
                      </Col>
                      <Col xs={9} className={'content'}>
                        saileshkotha
                      </Col>
                    </Row>
                    <Row className={'contact-profile-item'}>
                      <Col xs={3} className={'contact-profile-item-name'}>
                        Address
                      </Col>
                      <Col xs={9} className={'content'}>
                        1215 11TH ST, West Des Moines, IA 50265
                      </Col>
                    </Row>
                  </div>
                  <div className={'contact-map'}>
                    <LazyLoad>
                      <MapContainer/>
                    </LazyLoad>
                  </div>
                </div>
              </Fade>
            </Col>
          </Row>
        </Container>
        <div className={'connect-social'}>
          <Fade bottom duration={700} distance={'50px'}>
            <ul className={'social-bar'}>
              {
                socials.map((social) => {
                  return (
                    <li key={social.name}>
                      <a href={social.href} target="_blank" aria-label={social.name} rel="noopener noreferrer">
                        <i className={social.iconClass}/>
                      </a>
                    </li>
                  );
                })
              }
            </ul>
          </Fade>
        </div>
      </div>
    );
  }
}

Connect.propTypes = {
  submitForm: propTypes.func,
  form: propTypes.object
};

function mapStateToProps(state) {
  return {
    form: state.form
  };
}

const dispatches = {
  submitForm
};

export default connect(mapStateToProps, dispatches)(Connect);
