import React, {Component} from 'react';
import {connect} from 'react-redux';

import './About.css';

import Container from 'reactstrap/es/Container';
import Row from 'reactstrap/es/Row';
import Col from 'reactstrap/es/Col';
import Button from 'reactstrap/es/Button';
import Fade from 'react-reveal/Fade';
import LazyLoad from 'react-lazyload';

import socials from '../shared/data/socials';

import {downloadResume} from '../../actions/downloadActions';
import CaptchaModal from '../shared/CaptchaModal';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      isModalOpen: false
    };

    this.download = this.download.bind(this);
    this.modalToggle = this.modalToggle.bind(this);
  }

  componentDidMount() {

  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  download(fileName, captchaToken) {
    setTimeout(()=>{
      this.setState(prevState => ({
        isModalOpen: !prevState.isModalOpen
      }));
      this.props.downloadResume(fileName, captchaToken);
    }, 500);
  }

  modalToggle(fileName){
    this.setState(prevState => ({
      isModalOpen: !prevState.isModalOpen,
      fileName
    }));
  }

  render() {
    return (
      <div style={{paddingTop: 100}}>
        <LazyLoad>
          <div className={'head-bg'}/>
        </LazyLoad>
        <Container className={'about-container'}>
          <div className={'profile'}>
          <Row>
            <Col className={'align-self-center'}>
              <h1 className={'align-text-centre'}>
                <span style={{fontWeight: 300, textAlign: 'center'}}>I'm</span> Sailesh Kotha
              </h1>
              <h2 className={'profile-position align-text-centre'}>
                Software Engineer, Amazon Web Services Developer
              </h2>
              <hr/>
              <div className={'profile-details-section'}>
              <Row className={'profile-item'}>
                <Col xs={3} className={'profile-item-name'}>
                  Address
                </Col>
                <Col xs={9} className={'content'}>
                  1215 11TH ST, West Des Moines, IA 50265
                </Col>
              </Row>
              <Row className={'profile-item'}>
                <Col xs={3} className={'profile-item-name'}>
                  Email
                </Col>
                <Col xs={9} className={'content'}>
                  saileshkotha@outlook.com
                </Col>
              </Row>
              <Row className={'profile-item'}>
                <Col xs={3} className={'profile-item-name'}>
                  Phone
                </Col>
                <Col xs={9} className={'content'}>
                  +1 (602) 820-1615
                </Col>
              </Row>
              <Row className={'profile-item'}>
                <Col xs={3} className={'profile-item-name'}>
                  Focus
                </Col>
                <Col xs={9} className={'content'}>
                  MicroServices Architecture
                </Col>
              </Row>
              </div>
            </Col>
          </Row>
          </div>
          <div className={'profile-social'}>
            <ul className={'social-bar'}>
              {
                socials.map((social)=>{
                  return (
                    <li key={social.name}>
                      <a href={social.href} target={social.name === 'phone' || social.name === 'email'?'':'_blank'} aria-label={social.name} rel="noopener noreferrer">
                        <i className={social.iconClass}/>
                      </a>
                    </li>
                  )
                })
              }
            </ul>
          </div>
        </Container>
        <Container className={'summary-container'}>
          <p>
            <Button onClick={this.modalToggle.bind(this, 'Resume.docx')} size={'lg'} color={'border'} className={'ripple'}>
              Download Resume
            </Button>
          </p>
          <Fade top distance={'50px'}>
          <p>
            Hello! I'm Sailesh Kotha. Software Engineer and Amazon Web Services Developer specializing in MicroServices Architecture. Experienced in all stages of development cycle. Well-versed in numerous programming languages including Node.js, Python, JavaScript, Scala, NoSQL and background in dev ops.
          </p>
          </Fade>
        </Container>
        <CaptchaModal download={this.download} fileName={this.state.fileName} isModalOpen={this.state.isModalOpen} modalToggle={this.modalToggle}/>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    default: state.default
  };
}

export default connect(mapStateToProps, {downloadResume})(App);
