import React, {Component} from 'react';
import {connect} from 'react-redux';

import './Education.css';

import Container from 'reactstrap/es/Container';
import Fade from 'react-reveal/Fade';

import schools from '../shared/data/schools';

class Education extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isMobile: this.isMobile(window.innerWidth)
    };
  }

  isMobile(width){
    return (width <= 768)
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ isMobile: this.isMobile(window.innerWidth) });
  };

  render() {
    return (
      <div>
        <Container className={'education-container'}>
          <h2 className={'title'}>Education</h2>
          <ul className="timeline">
            {
              schools.map((school, i)=>{
                return (
                  <Fade key={school.name} left={!(i%2) && !this.state.isMobile} right={Boolean(i%2) || this.state.isMobile} distance={'30px'} duration={700}>
                  <li className={i%2?'timeline-inverted':''}>
                    <div className="timeline-badge">
                      <i className="fa fa-circle"/>
                    </div>
                    <div className="timeline-panel">
                      <div className="date">{school.date}</div>
                      <div className="timeline-heading">
                        <h4>{school.name}</h4>
                      </div>
                      <h4 className={'role'}>{school.role}</h4>
                    </div>
                  </li>
                  </Fade>
                )
              })
            }
            <li className="clearfix no-float"></li>
          </ul>
        </Container>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    default: state.default
  };
}

export default connect(mapStateToProps, {})(Education);
