import types from '../actionTypes';

export default (state = null, action) => {
  switch(action.type) {
    case types.submitFormSuccessResponse:
      return Object.assign({}, state, {
        inProgress: false,
        done: true
      });
    case types.resetFormStatus:
      return Object.assign({}, state, {
        inProgress: false,
        done: false
      });
    default:
      return state;
  }
}
